<template>

<v-list class="pt-0 pb-7" dense max-height=3pt >
    <v-subheader>筛选条件</v-subheader>
    <template v-for="(item, i) in filters">
        <base-filter
        :key=i
        :index="i"
        :all_filter_values="all_filter_values"
        :fields="filter_fields"
        @get_filter_result="get_filter_result"
        @update_field_values="update_field_values"
        @delete_filter="delete_filter"
        @add_filter="add_filter"
    />
    </template>
</v-list>
</template>

<script>
import { sync,get} from 'vuex-pathify'
export default {
    components: {
        BaseFilter:() => import('./Filter'),
    },
    data:()=>({
        // 通过列表显示筛选
        filters:[{field:"",filter_type:"",value:""}],
    }),
    computed: {
        collection_config:sync('db/collection_config'),
        all_filter_values:sync('db/all_filter_values'),
        filter_fields:get('db/filter_fields'),
    },
    methods:{
        // 返回筛选结果
        get_filter_result(index,field,filter_type,filter_value){
            // console.log("收到筛选结果",index,field,filter_type,filter_value);
            this.filters[index]={field:field,filter_type:filter_type,value:filter_value};
            this.$store.dispatch("db/update_query",this.filters);
        },
        // 更新字段筛选
        update_field_values(field){
            // console.log("更新字段筛选结果,此方法提交给store",field);
            this.$store.dispatch("db/update_filter_values",{field:field,all_filter_values:this.all_filter_values});
        },
        // 删除一个筛选条件
        delete_filter(index){
            console.log("删除",index);
            this.filters.splice(index,1);
        },
        // 添加一个筛选条件
        add_filter(){
            this.filters.push({field:"",filter_type:"",value:""});
        }
    }
}
</script>